.navbar {
    opacity: 1;
    color: black;
    transition: 0.5s linear;
    /* border-bottom: 2px solid #616161; */
    height: 120px;
    font-family: 'Montserrat', sans-serif;
    box-shadow: none;
    position: relative;
    z-index: 1000;
}

#header-bars {
    color: white;
    width: 40px;
    height: 40px;
    font-size: 25px;
border: 1px solid white;
line-height: 35px;
border-radius: 5px;
}
#header-bars:hover {
 background: #dddddd;
 color: grey;
}

.navbar:hover {
    opacity: 1;
}

/* For the effects when the scrolling starts */

html:not([data-scroll="0"]) body {
    padding-top: 3em;
}

html:not([data-scroll="0"]) .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    /* background-color: #fff; */
    opacity: 1;
    height: 110px;
    box-shadow: 0 0 0.5em rgba(0, 0, 0, 0.5);
    transition: 0.4s linear;
    background-color: rgb(36, 36, 36);
    color: white;
}

/* For the effects when the scrolling starts */

.t-none {
    transition: none;
}

#navbarSupportedContent .navbar-nav .nav-item .nav-link {
    color: white;
    background: transparent;
    text-align: center;
    border: none;
    font-weight: normal;
    font-size: 13px;
    margin-left: 10px;
    letter-spacing: 1px;
    cursor: pointer;
}

#navbarSupportedContent .navbar-nav .nav-item .nav-link:hover {
    font-weight: bold;
    color: #d6ad34;
    transition: none;
    /* transition: 0.2s linear; */
}

.img-connect-meta {
    border: 2px solid #666666;
    border-radius: 50%;
    cursor: pointer;
}

