/* Media Queries */
@media all and (max-width:600px) {
    #navbarSupportedContent .navbar-nav .nav-item .nav-link:hover {
        border: medium none currentColor;
        border: initial;
        border-radius: 0;
        border-radius: initial;
    }
    #logo-text {
        font-weight: 500;
        font-size: 40px;
        padding-left: 12%;
        letter-spacing: 3px
    }
    #navbarSupportedContent {
        margin-left: 0;
        margin-left: initial;
        margin-top: 5%;
        background: #9a771f;;
        border: 1px solid grey;
        border-radius: 3px;
    }
    html:not([data-scroll="0"]) #navbarSupportedContent {
        background: #9a771f;
        border: 1px solid grey;
        color: white;
        margin-top: 5%;
        transition: 0.5s linear;
    }
    html:not([data-scroll="0"]) #navbarSupportedContent .navbar-nav .nav-item .nav-link {
        color:white;
        background: transparent;
    }
    #navbarSupportedContent .navbar-nav .nav-item .nav-link {
        color: white;
        background: transparent;
        text-align: center;
        border: none;
        font-weight: normal;
        font-size: 13px;
        margin-left: 10px;
        letter-spacing: 1px;
        cursor: pointer;
    }
    
}
